@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&display=swap');

@layer base {
    body{
        @apply font-[Raleway]
    }
    li {
        @apply px-4;
        @apply cursor-pointer;
    }
}

.backgroundImg {
    background-repeat: no-repeat;
    
    background-position: center;
    height: 148px;
}

.centerText {
    text-align: center;
}

.special-word { 
    color: red;     
  } 

  